@import '../../../assets/styles/vars';

.create__pet__form {
    padding: 0px 80px;
    margin: 0 auto;
    width: 72vw;
    @media (max-width: 640px) {
        width: 100vw;
        padding: 0px 24px;
    }
    .pet__create__image__wrapper {
        text-align: center;
        margin-bottom: 48px;
        p {
            margin-top: 8px;
            font-family: $text-font;
        }
    }
    .pet__create__image__upload__wrapper{
        display:flex;
        align-items:center;
        justify-content:center;
        .pet__create__image__upload {
            height:120px;
            width:120px;
            border-radius: 50%;
            position:relative;
            display:flex;
            justify-content:center;
            align-items: center;  
            background-image: linear-gradient(to bottom, $third-color 50%, $white 50%);
            background-size: 100% 200%;
            transition: all 1s;
            color: #FFFFFF;
            font-size:64px;
            input[type='file'] {
                height:120px;
                width:10px;
                position:absolute;
                top:0;
                left:0;
                opacity:0;
            }
            &:hover{
                background-position: 0 -100%;
                color:$third-color;
            }
        }
    }
    .pet__create__image__chosen {
        height: 200px;
        padding-top: 40px;
        .pet__create__image__name {
            font-family: $text-font;
            text-align: center;
            font-size: 20px;
            i {
                color: black;
            }
        }
        .pet__create__image__remove {
            font-family: $text-font;
            text-align: center;
            font-size: 14px;
            background-color: $third-color;
            border-radius: 16px;
            width: 8vw;
            padding: 8px 16px;
            margin: 0 auto;
            color: $white;
            cursor: pointer;
            @media (max-width: 640px) {
                width: 40vw;
            }
        }
    }
    label {
        font-family: $text-font;
    }
    input {
        font-family: $text-font;
        border: none !important;
        background-color: #F3F3F3 !important;
        border-radius: 8px !important;
        padding: 8px 16px !important;
        &:focus {
            background-color: #F3F3F3 !important;
        }
    }
    .form-group {
        padding-left: 0px !important;
        padding-right: 0px !important;
    }
    select {
        font-family: $text-font;
        border: none !important;
        background-color: #F3F3F3 !important;
        border-radius: 8px !important;
        padding: 8px 16px !important;
        &:focus {
            background-color: #F3F3F3 !important;
        }
    }
    textarea {
        font-family: $text-font;
        border: none !important;
        background-color: #F3F3F3 !important;
        border-radius: 8px !important;
        padding: 8px 16px !important;
        box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.123);
        &:focus {
            background-color: #F3F3F3 !important;
        }
    }
    .create__pet__contact__warning {
        font-family: $text-font;
        color: rgb(255, 89, 89) !important;
    }
    .create__pet__submit {
        width: 100%;
        background-color: $third-color;
        border-radius: 16px;
        border: none;
        transition: .5s;
        margin-top: 8px;
        box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.123);
        padding: 8px;
        &:hover {
            border-radius: 4px;
            background-color: $third-color;
        }
        &:focus {
            border-radius: 4px;
            background-color: $third-color;
        }
    }
}